










import { Component, Vue } from 'vue-property-decorator'
import { wjhTool } from '../../util/wjhJS.js'

@Component({
  components: {

  }
})
export default class SensitiveWord extends Vue {
  value = '' // 词段
  columns1: object[] = [
    {
      title: '序号',
      key: 'id'
    },
    {
      title: '词段',
      key: 'word'
    },
    {
      title: '开始监测时间',
      key: 'date',
      render (h: any, params: any) {
        return h('span', wjhTool.dateFormat(+(params.row.date + '000'), 'y-M-dd hh:m'))
      }
    },
    {
      title: '监测后出现的数量',
      key: 'count'
    }
  ]
  data1:swdata[] = [
    {
      id: '1',
      word: '麦兜',
      date: 1550452403,
      count: 10
    },
    {
      id: '2',
      word: '豆子',
      date: 1552452403,
      count: 0
    },
    {
      id: '3',
      word: '葫芦娃',
      date: 1530452403,
      count: 0
    }
  ]

  addWord () {
    this.$axios.post('add/word', { content: this.value }).then((res: any) => {
      this.$Notice.success({
        title: `添加成功！`
      })
      this.getWord()
    })
  }

  getWord () {
    this.$axios.get('get/word').then((res: any) => {
      const data:swdata[] = res.data
      this.data1 = data
    })
  }

  mounted () {
    this.getWord()
  }
}
